html.js .loader {
  background: none repeat scroll 0 0 rgba(255, 255, 255, 0.9);
  bottom: 0;
  height: 100%;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 9999;
}

html.js .loader_inner {
  height: 100px;
  width: 100px;
  margin-top: -30px;
  margin-left: -30px;
  left: 50%;
  top: 50%;
  position: absolute;
}

@keyframes rotate__circle {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loader__circle {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  margin-top: -15px;
  left: 50%;
  margin-left: -15px;
  border-radius: 90%;
  border: 8px solid #c7b3a8;
  opacity: .98;
  z-index: 1;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  animation: rotate__circle 1s linear infinite;
}

.loader__circle:after {
  content: '';
  position: absolute;
  top: -2px;
  left: -3px;
  width: 10px;
  height: 10px;
  border-top: 1px solid #E0E0E0;
  transform: rotate(-45deg);
  border-radius: 90%;
}

.loader__circle:before {
  content: '';
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 90%;
  background: #c7b3a8;
}

.musician__loader {
  z-index: 99;
  position: relative;
  width: 100px;
  height: 100px;
  display: -webkit-flex;
