  overflow: hidden;
  position: relative;
}

.bar {
  background: #000;
  bottom: 0px;
  height: 3px;
  position: absolute;
  width: 3px;
  animation: sound 0ms -800ms linear infinite alternate;
}

.bar:nth-child(1) {
