}

.bar:nth-child(5) {
  left: 21px;
  animation-duration: 400ms;
}

.bar:nth-child(6) {
  left: 26px;
  animation-duration: 427ms;
}

.bar:nth-child(7) {
  left: 31px;
  animation-duration: 441ms;
}

.bar:nth-child(8) {
  left: 36px;
  animation-duration: 419ms;
}

.bar:nth-child(9) {
  left: 41px;
  animation-duration: 487ms;
}

.bar:nth-child(10) {
  left: 46px;
  animation-duration: 442ms​;
}

@-webkit-keyframes sound {
  0% {
    opacity: .35;
