/* Topline */
.topline {
  background: rgba(0, 0, 0, 0.1);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
  transition: background 0.2s linear;
}

.topline.--dark {
  background: rgba(0, 0, 0, 0.7);
  transition: background 0.2s linear;
  box-shadow: 0px -1px 15px 1px rgba(0, 0, 0, 0.5);
}

.topline.--dark .topline__menu a {
  color: #fff;
}

.topline.--dark .topline__menu a:hover {
  color: #fff;
}

.topline.--dark .topline__menu a:hover:before {
  background: #fff;
}

@media only screen and (max-width: 992px) {
  .topline.--dark .topline__menu a:hover {
    color: #727272;
  }
}

.topline.--dark .topline__menu a.active:before {
  background: #fff;
}

.topline.--dark .menu__toggle span {
  background: #fff;
}

.topline__menu {
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

@media only screen and (min-width: 992px) {
  .topline__menu {
    display: flex !important;
  }
}

@media only screen and (max-width: 992px) {
  .topline__menu {
    display: none;
    flex-direction: column;
    height: 100%;
    max-height: calc(100vh - 100px);
    overflow-y: auto;
    padding: 10px 0;
  }
  .topline__menu li {
    display: block;
  }
  .topline__menu li a {
    transition: background 0.2s linear, padding 0.2s linear;
  }
  .topline__menu li a:hover {
    padding: 0 10px;
    background: #fff;
    transition: background 0.2s linear, padding 0.2s linear;
  }
  .topline__menu li a:before {
    display: none;
  }
}

.topline__menu li {
  display: inline-block;
}

.topline__menu li a {
  display: block;
  font-family: 'Dosis', sans-serif;
  text-transform: uppercase;
  font-size: 17px;
  line-height: 3.47059;
  position: relative;
  color: #727272;
}

.topline__menu li a:before {
  opacity: 0;
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 10px;
  height: 2px;
  background: #000;
  transition: opacity 0.2s linear, color 0.2s linear;
}

.topline__menu li a:hover {
  color: #5f5f5f;
}

.topline__menu li a:hover:before {
  opacity: 1;
  transition: opacity 0.2s linear, color 0.2s linear;
}

.topline__menu li a.active:before {
  opacity: 1;
}

.menu__toggle {
  display: none;
  width: 50px;
  height: 50px;
  float: right;
  border: 0;
  background-color: transparent;
  position: relative;
}

.menu__toggle span {
  position: absolute;
  display: block;
  width: 70%;
  left: 50%;
  margin-left: -18px;
  height: 4px;
  background: #000;
  border-radius: 2px;
}

.menu__toggle span:nth-of-type(1) {
  top: 12px;
  transition: top 0.2s linear, transform 0.2s linear;
}

.menu__toggle span:nth-of-type(2) {
  top: 50%;
  margin-top: -2px;
  transition: opacity 0.2s linear;
}

.menu__toggle span:nth-of-type(3) {
  bottom: 12px;
  transition: bottom 0.2s linear, transform 0.2s linear;
}

@media only screen and (max-width: 992px) {
  .menu__toggle {
    display: block;
  }
}

.menu__toggle.--active span:nth-of-type(1) {
  top: 23px;
  transform: rotate(45deg);
  transition: top 0.2s linear, transform 0.2s linear;
}

.menu__toggle.--active span:nth-of-type(2) {
  opacity: 0;
  transition: opacity 0.2s linear;
}

.menu__toggle.--active span:nth-of-type(3) {
  bottom: 23px;
  transform: rotate(-45deg);
  transition: bottom 0.2s linear, transform 0.2s linear;
}

